import BASE_PATH from '@/api/basePath'
const API = {
  QUERYALL (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppShAction!queryAll.do', params)
  },
  QUERYDSHINFO (context, params) {
    return context.$http.post(BASE_PATH + 'zhfwgl/leave/leaveManagerAppShAction!queryDshInfo.do', params)
  }
}

export default API



