import API from '@/api/index'
import { MessageBox, Indicator, Toast } from 'mint-ui'
export default {
  data () {
    return {
      token: '',
      jobType: '', // 职位类型
      list: [],
      haveData: true, // 是否有待审批数据
      costomno: '', // 窗口  审批局
      ifcheck: '', // 是否审核
      ifvalidate: '', // 是否办理
      perpage: 10, // 一页10条
      pageNo: 1, // 当前第几页
      showMore: false // 是否显示更多
    }
  },
  created () {
    let data = this.$store.getters.getUser.data
    this.jobType = data.jobType
    this.token = this.$store.getters.getUser.token
    this.listData(this.jobType)
  },
  methods: {
    listData (jobType) {
      if (jobType === '01' || jobType === '02') {
        this.haveData = false
      } else if (jobType === '03' || jobType === '04' || jobType === '06' || jobType === '08') {
        this.ifcheck = '01'
        this.ifvalidate = 'db'
        if (jobType === '03') {
          this.costomno = '01'
        } else if (jobType === '04' || jobType === '06' || jobType === '08') {
          this.costomno = '02'
        }

        let param = {
          zhgl_qj01027: this.costomno,
          zhgl_qj01018: this.ifcheck,
          validate: this.ifvalidate,
          token: this.token,
          perpage: this.perpage,
          pageNo: this.pageNo
        }
        Indicator.open({
          text: '正在加载...',
          spinnerType: 'fading-circle'
        })

        API.QUERYALL(this, param).then(rep => {
          let result = rep.body
          Indicator.close()
          if (result.success) {
            let data = rep.body.data
            if (data.length === 0) {
              this.haveData = false
              this.showMore = false
            } else {
              this.haveData = true
              this.list = data
              this.showMore = true
            }
          } else {
            MessageBox.alert(result.message, '操作提示')
          }
        }, (rep) => {
          Indicator.close()
          Toast({
            message: '服务器异常',
            position: 'middle',
            duration: 1000
          })
        })
      } else if (jobType === '05' || jobType === '07') {
        let { token, perpage, pageNo } = this
        API.QUERYDSHINFO(this, { token, perpage, pageNo }).then((rep) => {
          let result = rep.body
          if (result.success) {
            let data = result.data
            if (data.length === 0) {
              this.haveData = false
              this.showMore = false
            } else {
              this.haveData = true
              this.list = data
              this.showMore = true
            }
          } else {
            MessageBox.alert(result.message, '操作提示')
          }
        }, (rep) => {
          Indicator.close()
          Toast({
            message: '服务器异常',
            position: 'middle',
            duration: 1000
          })
        })
      }
    },
    // 点击更多
    moreApprovel () {
      if (this.jobType === '01' || this.jobType === '02') {
        MessageBox.alert('普通员工暂无审批权限', '操作提示')
      } else {
        this.$router.push({ name: 'checkTab' })
      }
    },
    toClockIn () {
      this.$router.push({ name: 'clockIn' })
    },
    toCheckTab () {
      if (this.jobType === '01' || this.jobType === '02') {
        MessageBox.alert('普通员工暂无审批权限', '操作提示')
      } else {
        this.$router.push({ name: 'checkTab' })
      }
    },
    toCheckRecord () {
      this.$router.push({ name: 'clockInRecord' })
    },
    toVacation () {
      this.$router.push({ name: 'vacationList' })
    },
    toCheckDetail (id, type) {
      this.$store.commit('setVacationId', { data: id })
      this.$store.commit('setMenuType', { data: type })
      if (type === '01') { // 窗口
        this.$router.push({ name: 'vacationCheckWindow' })
      } else if (type === '02') { // 审批局
        this.$router.push({ name: 'vacationCheckDept' })
      }
    }
  }
}



